import React, { useState, useEffect } from "react";
import axios from "axios";

function App() {
    const [rows, setRows] = useState([])
    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/rows/"
        }).then(response => {
            setRows(response.data.results)
        })
    }, [])

    return(
        <div className="App">
            <h1>Table</h1>
            <table>
                <tr>
                    <th>Дата</th>
                    <th>Название</th>
                    <th>Количество</th>
                    <th>Расстояние</th>
                </tr>
                {rows.map(row => (
                    <tr key={row.id}>
                        <td>{row.date}</td>
                        <td>{row.title}</td>
                        <td>{row.count}</td>
                        <td>{row.distance}</td>
                    </tr>
                ))}
            </table>
        </div>
    );
}

export default App;
